<template>
    <div class="center" >
        <div class="center_breadcrumb">
            <crumb></crumb>
        </div>
        <div class="center_content">
            <div class="center_content_search" style="padding-bottom:18px;padding-top: 0px;">
                <div class="center_content_search_left">
                    <div class="search-input-suffix gap" style="margin-top:18px">
                        <label>问卷类型：</label>
                        <el-select v-model="page.classifyId" placeholder="请选择">
                            <el-option
                            v-for="item in classifyOptions"
                            :key="item.id"
                            :label="item.classifyName"
                            :value="item.id">
                            </el-option>
                        </el-select>
                    </div>
                    <div class="search-input-suffix gap" style="margin-top:18px">
                        <label>起止时间：</label>
                         <el-date-picker
                            class="upicker"
                            v-model="times"
                            @change="timeChange"
                            type="datetimerange"
                            range-separator="-"
                            start-placeholder="请选择开始"
                            end-placeholder="结束时间">
                        </el-date-picker>
                    </div>
                    <div class="search-input-suffix" style="margin-top:18px">
                        <label>问卷搜索：</label>
                        <el-input class="search"
                                @keyup.enter.native="onSearch"
                                v-model="page.keyWord"
                                placeholder="请输入搜索关键词"
                                >
                        </el-input>
                        <div class="search--btn" style="margin-left: 20px" @click="onSearch">搜索</div>
                    </div>
                </div>
            </div>
            <div class="table-container">
                <el-table :data="tableData"  stripe style="width: 100%" v-loading="loading">
                    <el-table-column label="问卷名称" :show-overflow-tooltip="true">
                        <template slot-scope="scope">
                            {{ scope.row.formname}}
                        </template>
                    </el-table-column>
                    <el-table-column label="问卷类型" >
                        <template slot-scope="scope">
                            <span>{{ scope.row.classifyname }}</span>
                        </template>
                    </el-table-column>
                    <el-table-column label="开始时间">
                        <template slot-scope="scope">
                            <span>{{ scope.row.starttime | time}}</span>
                        </template>
                    </el-table-column>
                    <el-table-column label="结束时间">
                        <template slot-scope="scope">
                            <span>{{ scope.row.endtime | time}}</span>
                        </template>
                    </el-table-column>
                    <el-table-column label="操作" width="160">
                        <template slot-scope="scope">
                            <el-button type="text" size="small" @click="handlelock(scope.row)">
                                <svgIcon icon-class="iconxuexizhuangtaixiangqing"></svgIcon>&nbsp;&nbsp;查看
                            </el-button>
                        </template>
                    </el-table-column>
                </el-table>
                 <div class="center_pagination">
                    <el-pagination background @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="page.page" :page-sizes="[15, 30, 40, 50]" :page-size="page.rows"  layout="prev, pager, next, jumper, sizes, total" :total="total"></el-pagination>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import {formatDate} from '@/utils/utils.js'
export default {
    data() {
        return {
            page:{           
              startTime:'',
              endTime:'',
              classifyId:'',
              keyWord:'',
              page:1,
              rows:15
            },
            total: 0,
            loading: true,
            tableData: [],
            times:[],
            classifyOptions:[],
            api:{
                listFinish:'/form/rest/FormMainBaseBusiness/listFinish/v1',
                classify:'/form/rest/FormClassifyBusiness/listAll/v1',
            }
        };
    },
    mounted() {
        this.classifyAll()
        this.initData()    
    },
    methods: {
        initData() {
            this.$post(this.api.listFinish, this.page).then(res => {
                if (res.code === 200) {            
                    this.tableData = res.data.rows
                    this.total = res.data.total
                }
                 this.loading = false
            })
        },
         classifyAll(){
            this.$post(this.api.classify).then(res=>{
                if(res.code ===200){
                    this.classifyOptions=res.data
                    this.classifyOptions.unshift({
                        classifyName:'全部',
                        id:''
                    })
                }
            })
        },
        timeChange(){
            this.page.startTime=formatDate(Date.parse(this.times[0]), 'yyyy-MM-dd')
            this.page.endTIme=formatDate(Date.parse(this.times[1]), 'yyyy-MM-dd')
        },
        onSearch() {
           this.page.page=1
           this.page.rows=15
           this.initData()
        },
        handlelock(row){
            this.$router.push({
                path:'/Iaminvolved/result',
                query:{
                    formId:row.formid,
                    userId:row.userid
                    }
                })
        },
        handleSizeChange(val) {
            this.page.rows = val;
            this.initData()
        },
        handleCurrentChange(val) {
            this.page.page = val;
            this.initData();
        }
    },
    filters:{
     time(val){
         return val ? formatDate(val, 'yyyy-MM-dd') : ''     
     }
  }
};
</script>

<style lang="scss">
@import '../../styles/center.scss';
</style>



